import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'
import draggable from 'vuedraggable'
//引入echarts
import echarts from 'echarts'
Vue.prototype.$echarts = echarts
//解决在使用element-ui时出现警告[Violation] Added non-passive event listener to a scroll-blocking ‘mousewheel’ event. 
import 'default-passive-events'
//iconbtn
import IconBtn from '@/components/common/iconbtn/IconBtn.vue'
Vue.component('icon-btn', IconBtn)
//icon
import Icon from '@/components/common/icon/Icon'
Vue.component('icon', Icon)
//应用阿里图库
// import './assets/iconfont/iconfont.css'
//引入ElementUI
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import 'element-ui/lib/theme-chalk/display.css';
Vue.config.productionTip = false
Vue.use(ElementUI)
ElementUI.Dialog.props.closeOnClickModal.default = false
ElementUI.MessageBox.setDefaults({
closeOnClickModal: false
})
//全局应用axios
Vue.prototype.axios = axios
//全局应用vuedraggable
Vue.component('draggable', draggable)
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

export default Vue;