const tabledata = {
	namespaced: true,
	state: {
		form: {
			formTitle: '未命名',
			formDesc: '描述',
			templateList: []
		},
		index: 0,
		subjects: [],
		deletePicArr:[]
	},
	mutations: {
		update: function(state, items) {
			state.form.templateList = items
		},
		insert: function(state, data) {
			let index = 0
			let arr = []
      // 重新排序
      console.log(data)
			while (index < data.templateList.length) {
				for (let i in data.templateList) {
					if (data.templateList[i].templateCount === index) {
						arr.push(data.templateList[i])
					}
				}
				index++
			}
			//
			for (let r in data.templateList) {
				//设置表单select=false
				data.templateList[r].selected=false
				//如果表单的option不为空，就把他转成数组格式
				if (data.templateList[r].templateContent !== null && data.templateList[r].templateContent !== undefined && data.templateList[
						r].templateContent !== "") {
					data.templateList[r].templateContent = eval(data.templateList[r].templateContent)
				}
				//如果表单的urloption不为空，就把他转成数组格式
				if (data.templateList[r].templateImgUrl !== null && data.templateList[r].templateImgUrl !== undefined && data.templateList[
						r].templateImgUrl !== "") {
					data.templateList[r].templateImgUrl = eval(data.templateList[r].templateImgUrl)
				}
				//如果表单的名字是多选框，把存放数组地方改成[]
				if (data.templateList[r].templateName === "checkbox"||data.templateList[r].templateName === "piccheckbox") {
					data.templateList[r].data = []
				} else {
					data.templateList[r].data = ""
				}
			}
			data.templateList = arr
			state.form = data
		},
		//插入
		increment: function(state, data) {
			let isExist = false
			// console.log(data)
			if (state.form.templateList.length === 0) {
				data.templateCount = state.form.templateList.length
			}
			for (let r in state.form.templateList) {
				isExist = false

				for (let i in state.form.templateList) {
					if (parseInt(r) === state.form.templateList[i].templateCount) {
						isExist = true
						break
					}
				}
				if (!isExist) {
					data.templateCount = parseInt(r)
					break
				} else {
					data.templateCount = state.form.templateList.length
				}
			}
			//判断是否是图片
			if(data.templateName==='picradio'){
                state.deletePicArr.push({"templateCount":data.templateCount,"option":[]})
            }
			console.log(state.deletePicArr)
			state.form.templateList.push(data)
		},
		moveup: function(state, index) {
			state.form.templateList[index] = state.form.templateList.splice(index - 1, 1, state.form.templateList[index])[0]
		},
		movedown: function(state, index) {
			state.form.templateList[index] = state.form.templateList.splice(index + 1, 1, state.form.templateList[index])[0]
		},
		delete: function(state, index) {
			state.form.templateList.splice(index, 1)
		},
		setindex: function(state, index) {
			state.index = index
		},
		setsubjects: function(state, subjects) {
			state.subjects = subjects
		},
		commitdata:function(state,data){
			state.form.templateList[state.index]=data
		},
		cleanNewOption:function(state){
			state.newOption=[]
		},
		addDeletePicArr:function(state,templateCount){
			state.deletePicArr.push({templateCount,option:[]})
    },
    cleanPicArr:function(state,data){
      state.deletePicArr=data
    },
    setRequire:function(state,data){
      state.form.templateList[state.index].templateRequire=data
    }
	},
	getters: {
		form: state => state.form,
		data: state => state.form.templateList[state.index],
		subjects: state => state.subjects,
		index:state => state.index,
		deletePicArr:state => state.deletePicArr
	},
	actions: {}
}
export default tabledata
