import {
	request
} from './request'

/**
 * 获取某用户的所有表单
 */
export function getMyForm() {
	return request({
		// url: '/data/myForm.json'
		url: '/FormController/form'
	})
}

/**
 * 获取某用户的所有收藏表单
 */
export function getFavourForms() {
	return request({
		url: '/FormController/getFavourForms'
	})
}

/**
 * 获取该用户为别人填写的表单
 */
export function getFillesForms() {
	return request({
		url: '/FormController/getFillesForms'
	})
}


/**
 * @param {Object} formId
 * 收藏表单和取消收藏表单
 */
export function updateFavour(formId) {
	return request({
		method: "PATCH",
		url: '/FormController/updateFavour/' + formId
	})
}

/**
 * @param {Object} formId
 * 复制表单
 */
export function copyForm(formId) {
	return request({
		method: 'POST',
		url: '/FormController/copy/' + formId
	})
}

/**
 * @param {Object} formId
 * @param {Object} formName
 * 表单重命名
 */
export function updataDataName(formId, formName) {
	return request({
		method: 'PATCH',
		url: '/FormController/updateName/' + formId + '/' + formName
	})
}

/**
 * @param {Object} formId
 * 删除表单
 */
export function deleteFormByformId(formId) {
	return request({
		method: 'DELETE',
		url: '/FormController/form/' + formId
	})
}

export function formOpen(formId) {
	return request({
		method: 'PATCH',
		url: '/FormController/updateOpen/' + formId
	})
}
