import Vue from 'vue'
import VueRouter from 'vue-router'
//home
const Home = () => import('@/views/home/Home');
const MyForm = () => import('@/views/home/childComps/MyForm')
const Favorites = () => import('@/views/home/childComps/Favorites')
const ParticipatedForms = () => import('@/views/home/childComps/ParticipatedForms')

//template
const Templates = () => import('@/views/templates/Templates')
const TemplateList = () => import('@/views/templates/childViews/templelist/TemplateList')
const TemplateDetail = () => import('@/views/templates/childViews/templatedetail/TemplateDetail')

//user
//用户登录注册
const defaultLoginRegister = () => import('@/components/common/login/default')
const login = () => import('@/components/common/login/childComp/login')
const register = () => import('@/components/common/login/childComp/register')
//个人中心
const Profile = () => import('@/views/user/profile/ProFile')
const Setting = () => import('@/views/user/profile/childViews/Setting')


Vue.use(VueRouter)

const routes = [
	//myform--主页
	{
		path: '/',
		component: Home,
		redirect: '/home',
		meta: {
			index: 0
		},
		children: [
			{
				path: 'home',
				component: MyForm,
				meta: {
					index: 1
				}
			},
			{
				path: 'favorites',
				component: Favorites,
				meta: {
					index: 2
				}
			},
			{
				path: 'participated_forms',
				component: ParticipatedForms,
				meta: {
					index: 3
				}
			}
		]
	},
	//表单模板
	{
		path: '/templates',
		component: Templates,
		redirect: '/templates/list',
		children: [
			{
				path: "list",
				component: TemplateList
			},
			{
				path: 'detail',
				component: TemplateDetail
			}
		]
	},
	//编辑表单--数据。。。
	{
		path: '/formFill',
		name: 'EditPage',
		component: () => import('@/views/editpage/EditPage.vue'),
		redirect: '/formFill/maincontent',
		children: [
			{
				path: "maincontent/:id",
				name: "maincontent",
				component: () => import('@/components/EditPageComp/BodyComponent/Main.vue'),
			},
			{
				path: "summarypage/:id",
				name: "summarypage",
				component: () => import('@/components/EditPageComp/BodyComponent/summarypage.vue'),
			},
			{
				path: "formdata/:id",
				name: "formdata",
				component: () => import('@/components/EditPageComp/BodyComponent/formdata.vue'),
			},
			{
				path: "chartsdata/:id",
				name: "chartsdata",
				component: () => import('@/components/EditPageComp/BodyComponent/ChartsData.vue'),
			}
		]
	},
	//发布表单
	{
		path: '/f/:id',
		component: () => import('@/components/EditPageComp/BodyComponent/FillTable.vue'),
		meta: { requiresAuth: false }
	},
	//个人中心
	{
		path: '/profile',
		component:Profile,
		redirect: '/profile/setting',
		children: [
			{
				path: 'setting',
				component: Setting
			}
		]
	},
	//登录
	{
		path: '/user',
		component: defaultLoginRegister,
		redirect: '/user/login',
		children: [
			{
				path: 'login',
				component: login,
				meta: { requiresAuth: false }
			},
			{
				path: 'register',
				component: register,
				meta: { requiresAuth: false }
			}
		]
  },
  //表单填写完成
  {
		path: '/finish',
		component: () => import('@/components/EditPageComp/BodyComponent/FinishFill.vue'),
		meta: { requiresAuth: false }
  },
  // 表单未发布
  {
		path: '/stop',
		component: () => import('@/components/EditPageComp/BodyComponent/StopFill.vue'),
		meta: { requiresAuth: false }
  },
	//用户头像上传--测试（基本完成）
	{
		path: '/test',
		component: () => import('@/views/user/test'),
		meta: { requiresAuth: false }
	},
	//测试--excel
	{
		path: '/excel',
		component: () => import('@/components/content/tooltips/ImportExcel.vue'),
		meta: { requiresAuth: false }
	}
]

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes
})

router.beforeEach((to, from, next) => {
	if(to.meta.requiresAuth === false) {
		next()
	}else {
		const token = window.localStorage.getItem('token')
		if(token === undefined || token === null || token === '') {
			router.push('/user/login')
		}else {
			next()
		}
	}
	
})

//解决重复跳转至同一router，报错的问题
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}


export default router
