import {
	getMyForm,
	getFavourForms
} from '@/network/form.js'

const form = {
    namespaced: true,
    state: {
       formData: '',
    },
    mutations: {
			updateFormData(state, data) {
				state.formData = data
			}
    },
    getters: {
        getFormData: state => state.formData,
    },
    actions: {
			saveMyform(context) {
				getMyForm().then(res => {
					context.commit('updateFormData', res.data)
				})
			},
			saveFavorites(context) {
				getFavourForms().then(res => {
					context.commit('updateFormData', res.data)
				})
			}
		}
}

export default form