	import {
		getUserInfo
	} from "@/network/user.js";
const user = {
	namespaced: true,
	state: {
		userInfo: null
	},
	mutations: {
		updateUserInfo(state, data) {
			state.userInfo = data;
			localStorage.setItem('userInfo', JSON.stringify(data))
		}
	},
	getters: {
		getuserInfo: state => {
			if (state.userInfo === null) {
				state.userInfo = JSON.parse(localStorage.getItem('userInfo'))
			}
			return state.userInfo
		}
	},
	actions:{
		saveUserInfo({rootState, commit}) {
			return new Promise( resolve => {
				getUserInfo().then(res => {
					let userinfo = res.data
					if (rootState.getURL != undefined && rootState.getURL != null && rootState.getURL != '') {
						userinfo.userImg = rootState.getURL + userinfo.userImg;
					}
					commit('updateUserInfo', userinfo)
					resolve()
				})
			})
		}
	},
}

export default user
