<template>
	<div class="btn-item" @click="btnClick" :style="activeStyle" ref='icon'>
		<i :class="'iconfont ' + iconClass"  v-if="iconBtnType=='left'">
			<span class='tooltiptext' v-if="this.tips != ''">{{tips}}</span>
		</i>
		<slot name="btn-text">{{btnText}}</slot>
		<i :class="'iconfont ' + iconClass" v-if="iconBtnType=='right'">
		</i>
		<span class='tooltiptext' v-if="this.tips != ''">{{tips}}</span>
	</div>
</template>

<script>
	export default {
		name: 'IconBtn',
		props: {
			iconBtnType: {
				type: String,
				default: 'left'
			},
			path: String,
			iconClass: String,
			btnText: String,
			tips: {
				type: String,
				default: ''
			},
			activeColor: {
				type: String,
				default: 'var(--color-text-change)'
			},
		},
		computed: {
			isActive() {
				return this.$route.path.indexOf(this.path) != -1
			},
			activeStyle() {
				return this.isActive ? {color: this.activeColor}: {}
			}
		},
		methods: {
			btnClick() {
				this.$router.push(this.path).catch(err => err)
			}
		},
		mounted() {
			if(this.tips != '') {
				this.$refs.icon.className += ' tooltip'
			}
		}
	}
</script>

<style>
	.btn-item:hover {
		color: var(--color-text-change);
	}
	.btn-item> i {
		margin-right: 3px;
	}
	.btn-item {
		display: inline-block;
	}
</style>
